const HomePage = () => {

    const [wineInfos, setWineInfos] = useState([]);
    const [idx, setIdx] = useState(0)

    const fadeIn = useSpring({
        opacity: 1,
        transform: 'translateY(0%)',
        from: { opacity: 0, transform: 'translateY(-100%)' },
        delay: 400,
        config: { 
            ...config.wobbly
        }, 
    });

    const fadeInImg = useSpring({
        opacity: 1,
        y: 0,
        transform: 'translateY(0%)',
        from: { opacity: 0, y: 500, transform: 'translateY(50%)' },
        delay: 400,
        config: {
                mass: 1.1,
                tension: 60,
                friction: 14,
                precision: 0.001,
                velocity: 0.018
              },
    });

    const wait = () => {
        setTimeout(function(){ return true;},400);
    }

    const getWineInfos = async (url) => {

        let wineInfo = await urlMetadata(`https://cors-anywhere.herokuapp.com/https://www.saq.com/fr/${url}`)

        let newWineInfos = [...wineInfos]

        newWineInfos.push(wineInfo);
        window.wines.push(wineInfo);

        setWineInfos(newWineInfos);

        await wait

        return true;
    }

    const handlegetwines = (e) => {

        let promises = [];

        window.wines = [];

        /* getWineInfos(wines[wineInfos.length + 69]); */

        for (let i=69;i < wines.length;i++) {
            promises.push(getWineInfos(wines[i]));
        }

        Promise.all(promises).then(() => console.log(window.wines));
    }

    const showNext = () => {
        let newIdx = idx + 1;

        if (newIdx === data.length) {
            newIdx = 0;
        }

        /* if (newIdx === 1) {
            data.forEach((item) => {
                if (data.findIndex(item2 => item2.image === item.image) > -1) {
                    console.log(item.image)
                }
            })
        } */

        setIdx(newIdx);
    }

    const transitions = useTransition(data[idx], {
        from: {
          opacity: 0,
        },
        enter: {
          opacity: 1,
        },
        leave: {
          opacity: 0,
        },
        config: { duration: 1000 },
        onRest: () =>  setTimeout(function(){ showNext();}, idx === 0 ? 3000 : 3000),
      });

    return (
        <div className="h-full w-full flex flex-col items-center justify-start overflow-hidden">
            <animated.div className="select-none text-center leading-none font-title text-3xl mt-20 text-gray-800" style={{lineHeight:'2rem', fontSize:'3rem', ...fadeIn}}>
                Sommelier<br /><span className="leading-none font-title text-2xl" style={{lineHeight:'2rem', fontSize:'2.5rem'}}>du dimanche</span>
            </animated.div>
            <div className="h-full w-full flex flex-col items-center justify-start overflow-hidden">
                {transitions((props, item) => (
                    <animated.img /*onClick={handlegetwines}*/ key={item['og:url']} style={{height: isMobile ? '75%' : '85%', bottom: isMobile ? '-15%' : '-11%', ...fadeInImg,  ...props}} alt={item['og:title']} className="absolute bottom-0 object-scale-down" src={item.image.replace('&fit=bounds&height=265&width=265&canvas=265:265', '')} />
                ))}
            </div>
            
        </div>
    );
}

export default HomePage;